import React from 'react';
import { Motion, spring, presets } from 'react-motion';

class CharSpinner extends React.Component {

  constructor(){
    super();
    this.chars = '\', @-abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ.'.split('');
  }

  findPos = (char) => {
    return this.chars.indexOf(char);
  }

  render(){
    return (
      <Motion defaultStyle={{top: 0}} style={{top: spring(this.findPos(this.props.char), presets.wobbly) }}>
      {val => {
        let style = {
          position: 'absolute',
          top: (val.top*55)*-1
        }
        return (
          <div className="tile">
            <div style={style}>
              {this.chars.map(char => {
                let tileClass = 'tile-char';
                if(char === this.props.char){
                  tileClass += ' active '
                }
                return <div key={char} className={tileClass}>{char}</div>
              })}
            </div>
          </div>
        )
      }}
      </Motion>
    )
  }
}


class CharSpinners extends React.Component {
  constructor(){
    super();
  }
  render(){
    console.log('rendering spiners')
    let spinners = this.props.str.split('').map((char, i) => <CharSpinner key={i} char={char} />)
    return (
      <div className="spinners-holder">{spinners}</div>
    );
  }
}

class App extends React.Component {
  constructor(){
    super();
    this.state = {stdOut: 'Hi , I\'m Joe'}
  }

  setString(s){
    console.log(s)
    this.setState({stdOut: s})
  }

  render(){
    return (
      <div>
        <div className="tiles">
          <CharSpinners str={this.state.stdOut} />
        </div>
        <Controls m={this.setString.bind(this)} />
      </div>
    );
  }
}


class Controls extends React.Component {
  render(){
    let controls = [
          { txt: 'Twitter', content: "twitterbird" },
          { txt: 'Github', content: "githubalt" },
          { txt: 'egghead.io', content: "twitterbird" },
          { txt: 'LinkedIn', content: "linkedin" },
          { txt: 'YouTube', content: "youtube" },
          { txt: 'email', content: "email" }
    ]
    return  (
      <div className="tile-controls">
        {controls.map((control, i) => <Control key={i} m={this.props.m} txt={control.txt} content={control.content} />)}
      </div>
    );
  }
}

class Control extends React.Component {
  render(){
    return (
      <div className="control-item" onMouseOver={this.props.m.bind(null,this.props.txt)}><span className='symbol'>{this.props.content}</span></div>
    );
  }
}

React.render(React.createElement(App), document.getElementById('app'));



